import React from 'react'
import { OutboundLink } from 'gatsby-plugin-google-analytics'

import styles from './links.module.css'
import GitHub from "../../assets/icons/github.svg";
import LinkedIn from "../../assets/icons/linkedIn.svg";
import Mail from "../../assets/icons/mail.svg";

export default () => (
  <div className={styles.links}>

    <OutboundLink href="https://www.linkedin.com/in/arturo-e-stable" rel="noopener noreferrer" target="_blank" aria-label="Link Up!">
      <LinkedIn alt="LinkedIn icon" />
    </OutboundLink>

    <OutboundLink href="https://github.com/artystable" rel="noopener noreferrer" target="_blank" aria-label="Git Social!">
      <GitHub alt="GitHub icon" />
    </OutboundLink>
    
    <OutboundLink href="mailto:arturostable@gmail.com" rel="noopener noreferrer" aria-label="Good Ol' E-Mail">
      <Mail alt="Mail icon" />
    </OutboundLink>
    
  </div>
)
